#about {
    padding-bottom: 0;
}

.about-info {
    width: 60%;
    margin: auto;
}

.about-title {
    text-align: center;
    color: var(--color-dark);
    padding-bottom: 1rem;
}

.about-text {
    text-align: center;
    margin: 1rem 0 !important;
    color: gray;
}

.about-text b {
    color: var(--color-primary);
}

.instructor-grid {
    display: flex;
    align-items: center;
}

.hero-img {
    max-height: 100%;
    max-width: 100%;
    object-fit: contain;
    
}
.hero-caption {
    padding: 0 2rem;
}
.hero-caption .title {
    text-align: left;
    color: var(--color-dark);
    padding-bottom: 1rem;
}
.hero-caption .text {
    text-align: left;
    color: var(--color-primary);
}

.philosophy-grid {
    width: 100%;
    background-color: #EDEAE5;
    padding-top: 2rem;
    padding-bottom: 4rem;
}

.philosophy-grid .title {
    text-align: center;
    padding: 2rem 0;
    color: var(--color-dark);
}

.philosophy-grid .text{
    width: 80%;
    margin: auto;
    text-align: center;
    color: var(--color-dark);
    padding-bottom: 3rem;
}

.philosophy-btn {
    display: flex;
    flex-direction: column;
    width: 100%;
    background-color: var(--color-primary) !important;
}

.philosophy-btn:hover {
    background-color: white !important;
}

.philosophy-btn img {
    height: 3rem;
    width: 3rem;
}

.philosophy-btn .caption {
    padding-top: 1rem;
    color: var(--color-dark);
    text-transform: none;
}

.philosophy-btn:hover caption {
    color: var(--color-dark) !important;
}